<template lang="pug">
    v-card 
        v-toolbar
            v-toolbar-title 获取Token持有者信息
            v-spacer
            v-btn(icon @click="refresh")
                v-icon mdi-magnify

        v-container
            v-row 
                v-col(cols="12" sm="12" lg="12")
                    v-overflow-btn(v-model="token"
                                            :items="tokens"
                                            label="Token"
                                            editable
                                            dense)
                
        v-list
            tokenlistitem(
                v-for="tx in trc10holders"
                :key="tx.address"
                :address="tx.address"
                :balance="tx.balance"
            )

        v-pagination(
            v-model="page"
            class="my-4"
            :length="pages"
            :total-visible="7"
        )
</template>

<script>
import * as api from "@/apis/wswebapi"
export default {
    components: {
        tokenlistitem: () => import('@/views/trx/trxComponents/tokenlistitem'),
    },
    data: () => ({
    tokens:[{text:"OKT",value:"TSUEgZw4vuyxFQK1MaCrSw1U2Liw3vhN7r"},{text:"ODKT",value:"TSfNVLZahyCqzgG2TDeUrA6udYbrBY7tTE"}],
    token:"TSUEgZw4vuyxFQK1MaCrSw1U2Liw3vhN7r",
    trc10holders:[],
    total:20,
    page:1,
    pagesize:10,
    }),
    computed: {
    pages(){
        let pages= parseInt (this.total/this.pagesize);
        if (pages>1){
            return pages
        }
        return 1
    },
    start(){
        return (this.page-1)*this.pagesize;
    }
    },
    methods:{
        refresh(){
            this.page=1;
            let params = {
                "address": this.token,
                "count": true,
                "limit": this.pagesize,
                "start": this.start,
            }
            
            api.GetTrxTokenHolders(params).then(res=>{
                if (res.total>0){
                    this.total=res.total;
                    this.trc10holders=res.data;
                    
                    
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:"获取失败",type:"error"})
                }
            })
           
        },
        getpagedata(){
            let params = {
                "address": this.token,
                "conunt": true,
                "sort": "-timestamp",
                "limit": this.pagesize,
                "start": this.start,
            }
            
            api.GetTrxTokenHolders(params).then(res=>{
                if (res.total>0){
                    this.total=res.total;
                    this.trc10holders=res.data;
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:"获取失败",type:"error"})
                }
            })
        },
        
    },
    watch:{
        page(){
            this.getpagedata();
        }
    }
}
</script>